const calculateCanvasSize = (canvas, imageWidth, imageHeight) => {
  const parentDiv = canvas.parentElement;
  const availableWidth = parentDiv.clientWidth;
  const availableHeight = parentDiv.clientHeight;

  const widthRatio = availableWidth / imageWidth;
  const heightRatio = availableHeight / imageHeight;

  const scaleFactor = widthRatio < heightRatio ? widthRatio : heightRatio;

  return {
    width: imageWidth * scaleFactor,
    height: imageHeight * scaleFactor
  };
};

const drawImageToCanvas = image => {
  const canvas = document.getElementById("original-canvas");
  const canvasSize = calculateCanvasSize(canvas, image.width, image.height);
  canvas.width = canvasSize.width;
  canvas.height = canvasSize.height;
  var ctx = canvas.getContext("2d");
  ctx.drawImage(
    image,
    0,
    0,
    image.width,
    image.height,
    0,
    0,
    canvas.width,
    canvas.height
  );
};

const createImage = dataUrl => {
  const image = new Image();
  image.src = dataUrl;
  image.onload = () => drawImageToCanvas(image);
};

const readFile = file => {
  const fileReader = new FileReader();
  fileReader.onload = event => createImage(event.target.result);
  fileReader.readAsDataURL(file);
};

window.addEventListener("load", () => {
  document.getElementById("file-picker").onchange = event => {
    const file = event.target.files[0];
    readFile(file);
  };
});
